<template>
    <el-row type="flex" class="row-bg">
        <div class="block" justify="end">
            <span class="wrapper">
                <el-dialog
                    title="Adicionar Módulo"
                    :visible="isOpen"
                    size="large"
                    :show-close="false">
                    <el-form ref="form">
                        <el-form-item label="Informe o identificador">
                            <el-input placeholder="xxxxxxxx"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form ref="form">
                        <el-form-item label="Informe o nome">
                            <el-input placeholder="xxxxxxxx"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form ref="form">
                        <el-form-item label="Informe o cômodo">
                            <el-input placeholder="xxxxxxxx"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form ref="form">
                        <el-form-item label="Informe o tipo">
                            <el-input placeholder="xxxxxxxx"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button type="danger" @click="modalClick">Cancelar</el-button>
                        <el-button type="success" @click="modalClick">Adicionar</el-button>
                    </span>
                </el-dialog>
            </span>
        </div>
    </el-row>
</template>

<script>
export default {
  name: 'newModule',
  props: ['isOpen'],
  data () {
    return {
      input: ''
    }
  },
  methods: {
    modalClick: function () {
      this.$emit('modalClosed')
    }
  }
}
</script>

<style>
  .el-row {
    margin-bottom: 20px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
  }
  h1, h2 {
    font-weight: normal;
  }
  .subtitle h2 {
    color: gray;
    font-size: medium;
  }
  .el-input {
    width: 100%;
  }
</style>


