<template>
<el-collapse>
    <el-collapse-item :title="header">
      <el-row :gutter="12">
        <el-col :xs="24" :sm="24" :md="12" :lg="6" v-for="(module, index) in modules" :key="module.title">
          <module-card :module="module" v-on:stateChanged="onModuleStateChanged" :state="modulesState[module.identifier]" />
        </el-col>
      </el-row>
    </el-collapse-item>
</el-collapse>
</template>

<script>
  import ModuleCard from '../components/ModuleCard'

  export default {
    props: ['header', 'modules', 'modulesState'],
    components: { ModuleCard },
    data () {
      return {
      }
    },
    methods: {
      onModuleStateChanged: function (module, newState, state) {
        console.log(module.title + ' está ' + (newState ? 'ativado' : 'desativado'))
        this.$emit('moduleChanged', module, newState)
      }
    }
  }
</script>



