<template>
  <el-card class="box-card">
    <div slot="header" class="header clearfix">
      <h3>{{module.name}}</h3>
      <p>{{ getText() }}</p>
    </div>
    <el-switch
      v-model="switchState"
      on-text=""
      off-text="">
    </el-switch>
    <span>{{getText()}}</span>
  </el-card>
</template>

<script>
export default {
  props: ['module', 'state'],
  methods: {
    getText: function () {
      return this.state === true ? 'Ativado' : 'Desativado'
    }
  },
  computed: {
    switchState: {
      set: function (state) {
        this.$emit('stateChanged', this.module, state)
      },
      get: function () {
        return this.state
      }
    }
  }
}
</script>


<style>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  .clearfix:after {
      clear: both
  }

  .box-card {
    /* width: 300px; */
    margin-bottom: 20px;
  }
  .header * {
    margin: 0px 0px;
  }

  .el-switch {
    margin-right: 5px;
  }

</style>



