<template>
    <el-row type="flex" class="row-bg" justify="end">
        <div class="block" justify="end">
            <span class="wrapper">
                <el-button type="danger" size="large" @click="dialogVisible = true">Pedido de Socorro</el-button>
                <el-dialog
                    title="Mensagem de Socorro"
                    :visible.sync="dialogVisible"
                    size="large"
                    :close="false">
                    <center><span class="subtitle"><h2>Uma mensagem de socorro será enviada automaticamente para os seus contatos.</h2></span></center>
                    <el-form ref="form">
                        <el-form-item>
                            <el-input placeholder="Escreva sua mensagem aqui."></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button type="danger" @click="dialogVisible = false">Cancelar</el-button>
                        <el-button type="success" @click="dialogVisible = false">Enviar</el-button>
                    </span>
                </el-dialog>
            </span>
        </div>
    </el-row>
</template>

<script>
  export default {
    data () {
      return {
        dialogVisible: false,
        input: ''
      }
    },
    dialogFormVisible: false
  }
</script>

<style>
  .el-row {
    margin-bottom: 20px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
  }
  h1, h2 {
    font-weight: normal;
  }
 .subtitle h2 {
   color: gray;
   font-size: medium;
 }
</style>


