<template>
  <div>
    <header-title title="Dashboard" subtitle="Tudo certo, Administrador?" />
    <div>
      <room v-for="room in rooms" :key="room.id" :header="room.name" v-on:moduleChanged="onModuleChanged" :modules="room.modules" :modulesState="modulesState"></room>
    </div>
  </div>
</template>

<script>
import paho from 'paho-mqtt'
import HeaderTitle from '../components/HeaderTitle'
import Room from '../components/Room'
import api from '../api'

// let kitchenModules = [
//   { title: 'Torneira da Pia' },
//   { title: 'Luz' }
// ]

// let bedroomModules = [
//   { title: 'Abajour' }
// ]

// let areaModules = [
//   { title: 'Porta da Frente' }
// ]
console.log(paho)
let client = new window.Paho.MQTT.Client('iot.eclipse.org', 443, 'luna-ifrn-chico' + new Date())

export default {
  name: 'dashboard',
  components: { HeaderTitle, Room },
  data () {
    return {
      rooms: [],
      modulesState: {},
      loadingModules: true
      // kitchenModules,
      // bedroomModules,
      // areaModules
    }
  },
  methods: {
    onModuleChanged: function (module, state) {
      console.log('Dashboard achou', module, state)
      this.modulesState[module.identifier] = state
      let message = new window.Paho.MQTT.Message('1')
      message.destinationName = `m/${module.identifier}/${(state ? 101 : 102)}`
      client.send(message)
    }
  },
  beforeMount: function () {
    client.onMessageArrived = (message) => {
      console.log('recebeu')
      let module = message.destinationName.split('/')[1]

      this.modulesState[module] = (message.payloadString === '1')
      console.log(this.modulesState[module])
      console.log('topic: ' + message.destinationName + '\n' + 'message ' + message.payloadString)
    }

    this.$loading({ text: 'Carregando módulos' })
    client.connect({
      useSSL: true,
      onSuccess: () => {
        console.log('Cliente conectado')

        api.loadRooms().then(response => {
          console.log('rooms', response)
          this.rooms = response.data
          api.loadModules().then(response => {
            console.log('modules', response)
            const { data } = response
            this.rooms = this.rooms.map(r => ({ ...r, modules: data.filter(m => m.room_id === r.id) }))

            this.modulesState = data.reduce((c, n) => {
              let o = { ...c }
              o[n.identifier] = false
              return o
            }, {})

            data.forEach(module => {
              client.subscribe(`m/${module.identifier}/1/1`)
            })

            this.$loading().close()
          })
        })
      }
    })

    // setTimeout(() => {
    //   this.$loading().close()
    // }, 1000)
  }
}
</script>

<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



