<template>
  <div id="app">
    <toolbar />
    <help-button />
    <main>
      <el-row :gutter="20">
        <el-col :span="16" :offset="4">
          <div class="grid-content">
            <router-view></router-view>
          </div>
        </el-col>
      </el-row>
    </main>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar'
import HelpButton from '@/components/HelpButton'

export default {
  name: 'app',
  components: { Toolbar, HelpButton }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.el-row {
    margin-bottom: 20px;
    margin-top: 1.2em;
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
</style>



