<template>
  <header>
    <el-menu :default-active="'0'" class="toolbar" mode="horizontal" @select="itemSelected">
      <el-menu-item index="0">Logo</el-menu-item>
      <el-submenu index="2" class="right-item">
        <template slot="title">Administrador</template>
        <el-menu-item index="2-1">Atualizar Perfil</el-menu-item>
        <el-menu-item index="2-2">Sair</el-menu-item>
      </el-submenu>
      <el-menu-item index="1" class="right-item">Novo Módulo</el-menu-item>
    </el-menu>
    <new-module :isOpen="modalOpen" @modalClosed="modalClosed"></new-module>
  </header>
</template>

<script>
import NewModule from '@/components/NewModule'

export default {
  name: 'toolbar',
  components: { NewModule },
  methods: {
    itemSelected (key, keyPath) {
      console.log(key, keyPath)
      if (key === '1') {
        this.modalOpen = true
        console.log(this.modalOpen)
      }
    },
    modalClosed () {
      this.modalOpen = false
    }
  },
  data () {
    return {
      modalOpen: false
    }
  }
}
</script>

<style>
  .right-item {
    float: right !important;
  }
</style>



