import axios from 'axios'

const BASE_URL = `${(process.env.API_URL || 'http://localhost:8000')}/api/v1/`

const loadRooms = function () {
  return axios.get(BASE_URL + 'rooms/')
}

const loadModules = function () {
  return axios.get(BASE_URL + 'modules/')
}

export default {
  loadRooms,
  loadModules
}



